/* Styles généraux */
.home-container {
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    padding: 50px;
    box-sizing: border-box;
}

.acceuil, .section1, .section2, .section3, .section5 {
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
}

.acceuil {
    height: 750px;
}
  
.sectiona2{
    display: flex;
    flex-direction: row     ;
    align-items: center;
    margin-top: 20px;
    background-color: black;
    
}
.sectiona3{
    display: flex;
    flex-direction: row    ;
    align-items: center;
    margin-top: 20px;
    background-color: black;
    
}

/*la section photo et text*/
.sectiona1{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 20px;
    background-color:white;
}
.sectiona3-text,
.sectiona1-text,
.sectiona2-text{
    padding: 2rem;
    width: 100%;
    text-align: left;
    margin-bottom: 80px;
    line-height: 2.5;
    font-size: 18px;
}

.sectiona1-img{
    width: 100%;
    margin-bottom: 50px;
}
.sectiona5{
background-color: blueviolet;
}
/* Styles responsive pour les écrans plus petits */
@media (max-width: 768px) {
    .home-container {
        padding: 10px;
    }

    .sectiona2,
    .sectiona3,
    .sectiona1 {
        flex-direction: column;
    }

    .sectiona1-img
    .sectiona2-img{
        margin-bottom: 10px;
    }
    .sectiona3-text
    .sectiona1-text
    .sectiona2-text{
        padding: 0.5rem;
        margin-bottom: 15px;
    }
      
}